import React, { memo, useMemo } from "react";
import { useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import styled from "styled-components";
import HashLoader from "react-spinners/HashLoader";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { loginAction } from "../../redux/action/userAction";
import FormErrors from "../../common/components/FormErrors";
import useRedirect from "../../common/hooks/useRedirect";
const Login = ({ className, history }) => {
  const auth = useSelector((state) => state.userStatus.auth, shallowEqual);
  const role = useSelector(
    (state) => state.userStatus?.userInfo?.user?.role,
    shallowEqual
  );
  const error = useSelector((state) => state.error?.user_login, shallowEqual);
  const loading = useSelector(
    (state) => state.loading?.user_login,
    shallowEqual
  );
  useRedirect(
    useMemo(
      () => [
        { condition: role >= 1, to: "/agency-dashboard" },
        { condition: auth, to: "/user" },
      ],
      [role, auth]
    )
  );
  const { register, handleSubmit, errors } = useForm();
  const dispatch = useDispatch();
  const handle = (data) => {
    dispatch(
      loginAction(data, (role) => {
        if (role === 1) return history.push("/agency-dashboard");
        if (role === 0) return history.push("/getting-report");
        // console.log("====================================");
        // console.log(role);
        // console.log("====================================");
        return history.push("/");
      })
    );
  };
  return (
    <main className={`${className} form d-flex align-items-center flex-grow-1`}>
      <section className="form__form">
        <p
          className="fs__title--main fw-bold"
          style={{ textTransform: "uppercase" }}
        >
          Đăng nhập
        </p>
        {/* <p className="fs__text">Lorem ipsum dolor sit amet consectetur adipisicing elit.</p> */}
        <form onSubmit={handleSubmit(handle)}>
          <input
            type="text"
            name="name"
            placeholder="Username"
            disabled={loading}
            ref={register({ required: "Bạn cần nhập đầy đủ thông tin" })}
          />
          <input
            type="password"
            name="password"
            placeholder="Mật khẩu"
            disabled={loading}
            ref={register({ required: "Bạn cần nhập đầy đủ thông tin" })}
          />
          <FormErrors errors={errors} />
          <div className="error">{error}</div>
          <div className="text-end my-1">
            <a href="#">Quên mật khẩu</a>
          </div>
          <button
            type="submit"
            className="d-inline-flex justify-content-center align-items-center"
            disabled={loading}
          >
            {!loading ? (
              <div>Đăng Nhập</div>
            ) : (
              <HashLoader color="#fafafa" size={24} loading={true} />
            )}
          </button>
          {/* <input type="submit" value="Đăng Nhập">
            
          </input> */}
          <div className="my-1">
            <div className="text-center">
              Bạn chưa có tài khoản ? <Link to="/signup">Đăng ký</Link>
            </div>
          </div>
        </form>
      </section>
    </main>
  );
};

export default memo(styled(Login)`
  width: 100%;
  .form {
    /* &__img {
      width: 60%;
      display: flex;
      background-color: rgba(0, 0, 0, 0.4);
      flex-direction: column;
      justify-content: space-between;
      padding: 3rem 100px;
      position: relative;
      &::after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        background-image: url(/images/home3-slide-1.jpg);
        background-size: cover;
      }
      p {
        max-width: 400px;
        font-weight: bold;
        font-size: 2.5em;
      }
      &__logo {
        > img {
          width: 200px;
        }
      }
      &__link {
        a {
          margin-right: 10px;
          font-weight: bolder;
        }
      }
      @media ${(props) => props.theme.breakPoints.xl} {
        width: 50%;
      }
    } */
    &__form {
      text-align: center;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 0 40px;
      > form {
        margin: 0 auto;
        width: 600px;
        display: flex;
        flex-direction: column;
        input,
        button {
          /* &::placeholder {
            text-transform: uppercase;
          } */
          text-indent: 20px;
          background-color: black;
          color: #fafafa;
          height: 50px;
          margin: 10px 0;
          border-radius: 8px;
          border: 1px solid #fafafa;
          font-weight: bold;
          outline: none;
          transition: border 0.2s ease-in;
          &:focus {
            border: 3px solid ${(props) => props.theme.colorSecondary};
          }
        }
        input[type="submit"],
        button[type="submit"] {
          background-color: ${(props) => props.theme.colorSecondary};
          color: #fafafa;
          border-width: 3px;
          border-color: ${(props) => props.theme.colorSecondary};
          &:hover {
          }
        }
        input[type="date"] {
          text-indent: 10px;
          &::-webkit-calendar-picker-indicator {
            cursor: pointer;
            filter: invert(1);
            margin-right: 10px;
          }
        }
        input::-webkit-input-placeholder {
          color: #fafafa;
        }
      }
      /* @media ${(props) => props.theme.breakPoints.xl} {
        width: 50%;
      } */
      @media ${(props) => props.theme.breakPoints.lg} {
        width: 100%;
      }
      @media ${(props) => props.theme.breakPoints.sm} {
        > form {
          width: 100%;
        }
      }
    }
  }
`);
